import {Nav, Navbar, Container} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Link} from 'react-router-dom'

function NavigationBar() {
  return (
    <>
      <Navbar /*bg="light"*/ variant={"light"} style={{backgroundColor: '#ffcc59'}} expand="lg">
      <Container>
        <div className='logoBox'>
        <div className='logoImage'></div>
        <Navbar.Brand href="/">
          <h1 style={{color: '#043562'}}>
          IES - NTU
          </h1> 
        </Navbar.Brand>
        </div>
        <Navbar.Toggle aria-controls="navbarScroll" />
          <Navbar.Collapse>
            <Nav className="me-auto" style={{ maxHeight: '200px', width: "100%" }} navbarScroll>
              
              <Nav.Link as={Link} to="/">
                <div className='pagelink'> Home </div>
              </Nav.Link>
              <Nav.Link as={Link} to="events">
                <div className='pagelink'> Events </div>
              </Nav.Link>
              <Nav.Link as={Link} to="photos">
                <div className='pagelink'> Photo Gallery </div>
              </Nav.Link>
              <Nav.Link as={Link} to="corporate">
                <div className='pagelink'> Corporate Partners </div>
              </Nav.Link>
              <Nav.Link as={Link} to="committee">
                <div className='pagelink'> Committee </div>
              </Nav.Link>
              <Nav.Link as={Link} to="gec">
                <div className='pagelink'> GEC 2023 </div>
              </Nav.Link>
            </Nav>
            </Navbar.Collapse>
          </Container>
      </Navbar>      
    </>
  );
}

export default NavigationBar;