import React from 'react'
import Carousel from "../../components/carousel/Carousel.js"

import img8 from '../../assets/event.jpg';
import robotSee1 from '../../assets/Photo Gallery/Robots_see_1.jpg'
import robotSee2 from '../../assets/Photo Gallery/Robots_see_2.jpg'
import robotSee3 from '../../assets/Photo Gallery/Robots_see_3.jpg'
import MAETour1 from '../../assets/Photo Gallery/MAETour1.jpg'
import MAETour2 from '../../assets/Photo Gallery/MAETour2.jpg'
import MAETour3 from '../../assets/Photo Gallery/MAETour3.jpg'
import AGM1 from '../../assets/Photo Gallery/AGM1.jpg'
import studentLeadersNight from '../../assets/Photo Gallery/studentLeadersNight.jpg'

/* Note: All photos should be in a 4:3 aspect Ratio */

export const Photos = () => {
    return (
        <div className='container' /*style={{ maxWidth: 1200, marginLeft: 'auto', marginRight: 'auto', marginTop: 64 }}*/>
            <Carousel show={3}>

                <div className='image-container'>
                    <img src={robotSee1} alt="IES-NTU" style={{ width: '100%', height: '100%', aspectRatio: '3/2', objectFit: 'contain' }} />
                </div>

                <div className='image-container'>
                    <img src={MAETour1} alt="IES-NTU" style={{ width: '100%', height: '100%', aspectRatio: '3/2', objectFit: 'contain' }} />
                </div>

                <div className='image-container'>
                    <img src={robotSee3} alt="IES-NTU" style={{ width: '100%', height: '100%', aspectRatio: '3/2', objectFit: 'contain' }} />
                </div>

                <div className='image-container'>
                    <img src={AGM1} alt="IES-NTU" style={{ width: '100%', height: '100%', aspectRatio: '3/2', objectFit: 'contain' }} />
                </div>

            </Carousel>
            <div style={{margin: '20px'}}></div>
            <Carousel
                show={3}
            >
                <div>
                    <div className='image-container'>
                        <img src={MAETour2} alt="IES-NTU" style={{ width: '100%', height: '100%', aspectRatio: '3/2', objectFit: 'contain' }} />
                    </div>
                </div>
                <div>
                    <div className='image-container'>
                        <img src={robotSee2} alt="IES-NTU" style={{ width: '100%', height: '100%', aspectRatio: '3/2', objectFit: 'contain' }} />
                    </div>
                </div>
                <div>
                    <div className='image-container'>
                        <img src={MAETour3} alt="IES-NTU" style={{ width: '100%', height: '100%', aspectRatio: '3/2', objectFit: 'contain' }} />
                    </div>
                </div>
                <div>
                    <div className='image-container'>
                        <img src={studentLeadersNight} alt="IES-NTU" style={{ width: '100%', height: '100%', aspectRatio: '3/2', objectFit: 'contain' }} />
                    </div>
                </div>
            </Carousel>
        </div>
    )
}
