import React,{useState} from 'react';
import data from '../../assets/cardData';
import Modal from '../modal/Modal';

const Cards = () => {
    const [modal, setModal] = useState(false);
    const [tempdata, setTempdata] = useState([])
    const getData = (img, title, desc, rlink, rtext) => {
        let tempData = [img, title, desc, rlink, rtext];
        setTempdata(item => [1, ...tempData]);
        return setModal(true);
    }
    return (
        <>

            <section classname="py-4 py-lg-5 container">
                <div className="row justify-content-center align-item-center">
                    {data.cardData.map((item, index) => {
                        return (
                            <div className="col-11 col-md-6 col-lg-3 mx-0 mb-4" key={index}>
                                <div className="card p-0 overflow-hidden h-100 shadow">
                                    <img src={item.imgSrc} className="card-img-top" />
                                    <div className="card-body">
                                        <h5 className="card-title">
                                            {item.title}
                                        </h5>
                                        <p className="card-text">{item.descSmall}</p>
                                        <button className="btn btn-primary" style={{margin:"5px"}} onClick={() => getData(item.imgSrc, item.title, item.descBig, item.rlink)}>Details</button>
                                        <a href={item.rlink} target='_blank'>
                                        <button className="btn btn-success" style={{margin:"5px"}}>{item.rtext}</button>
                                        </a>
                                    </div>
                                </div>
                            </div>
                        )
                    })}
                </div>
            </section>
            {
                modal === true ? <Modal img={tempdata[1]} title={tempdata[2]} desc={tempdata[3]} hide={() => setModal(false)} /> : ''
            }

        </>
    )
}
export default Cards;